import React from 'react';

//Import to add Router elment for navigation
//Route element is used for setting url path to a component
//Switch makes sure that only one page renders at a time
import {
  BrowserRouter as Router,
  Route,
  Switch,
} from 'react-router-dom'

//Import to add page we created
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import NotFoundPage from './pages/NotFoundPage';
import ArticlePage from './pages/ArticlePage';
import ArticlesListPage from './pages/ArticlesListPage';
import NavBar from './NavBar';
//Import stylesheet for the document
import './App.css';


//First declearing the Router element
//NavBar called to be shown in all pages (here links are determinated)
//Router element attach url paths to components
function App() {
  return (
    <Router>
      <div className="App">
          <NavBar />
          <div id="page-body">
            <Switch>
              <Route path="/" component={HomePage} exact />
              <Route path="/about" component={AboutPage} exact />
              <Route path="/articles-list-page" component={ArticlesListPage} exact />
              <Route path="/article/:name" component={ArticlePage} exact />
              <Route component={NotFoundPage} />
            </Switch>
          </div>
      </div>
    </Router>
  );
}

export default App;
